// Frontend jQuery

jQuery(document).ready(function() {

//navigation toggle

	$('.menu-trigger').on("click", function () {
		$('body').toggleClass('showmenu')
	});



//mobile-panel
	$('body').on("click", ' [data-frontend="panel-headline"] ', function () {
		$( this ).next().toggle();
		$( this ).toggleClass( 'arrow-direction' );
	});

//all-panel
	$('body').on("click", ' [data-frontend="panel-headline-all"] ', function () {
		$( this ).next().toggle();
		$( this ).toggleClass( 'arrow-direction' );
	});

//prov order-2 to order-3
	if ( $( '.layout .order-1' ).length ) {
		$( '.layout .order-1' ).parent( '.layout' ).find( '.order-2' ).removeClass( 'order-2' ).addClass( 'order-3' );
	}

	$('select').on("click", function () {
	if ( $( '.double-select-wrap .active' ).length ) {
		$( '.double-select-wrap .active' ).prevAll( 'select' ).hide();
		$( '.double-select-wrap .active').show();
	}


	});



	$(' [data-frontend="show-edit-layout"]' ).click(function() {
		$( '.result' ).addClass( 'result-edit' );
	});


	$(' [data-frontend="show-loginlayer"] ').click(function() {
		$( '#loginblock' ).toggle();
	});


	//mobile-panel
		$('[data-frontend="aside-menu"]').on("click", 'li', function () {
			$( '[data-frontend="aside-menu"] li' ).removeClass('active');
			$( this ).toggleClass( 'arrow-direction' ).addClass('active');
			$( this ).toggleClass( 'arrow-direction' ).addClass('active');

			var liIndex = $(this).data('index');

			$( '[data-target]' ).hide();
			$( '[data-target=' + liIndex + ']' ).show();

		});

$( '.game span' ).after('<strong>/</strong>');

});
